import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import {
  Table,
  TableBody,
  TableFooter,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';
import injectTapEventPlugin from 'react-tap-event-plugin';

injectTapEventPlugin();

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      products: []
    }

    this.getProducts = this.getProducts.bind(this);
  }

  getProducts(products) {
    this.setState({
      products: products
    })
  }

  render() {

    console.log(this.state);

    let basket;

    basket = this.state.products.length ? <Basket products={this.state.products} /> : null

    return (
      <MuiThemeProvider>
        <div className="container">
          <AddProduct getProducts = {this.getProducts} />
          {basket}
        </div>
      </MuiThemeProvider>
    );
  }
}

class AddProduct extends Component {
  constructor(props) {
    super(props);

    this.state = {
      current: {
        name: '',
        price: '',
        errors: {
          nameError: '',
          priceError: ''
        }
      },
      products: []
    };

    this.handleFieldChange = this.handleFieldChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleFieldChange(e) {
    let current,
        errors = this.state.current.errors;

    if (errors[`${e.target.name}Error`] && e.target.value.length > 0) {
      errors[`${e.target.name}Error`] = '';
    }

    switch(e.target.name) {
      case 'name':
        current = {
          name: e.target.value,
          price: this.state.current.price,
          errors: errors
        };
        break;
      case 'price':
        current = {
          name: this.state.current.name,
          price: e.target.value,
          errors: errors
        };
        break;

      default:
        current = this.state.current;
    }

    this.setState({
      current: current
    })
  }

  handleClick() {
    let current = this.state.current,
        errors = {},
        isErr = false,
        products;

    for (let key in current) {

      if ( (key !== 'errors') && current[key].trim() === '' ) {
        errors[`${key}Error`] = 'Поле не должно быть пустым';
        isErr = true;
      }
    }

    if (isErr) {
      this.setState({
        current: {
          name: this.state.current.name,
          price: this.state.current.price,
          errors: errors
        }
      });
      return;
    } else {

      products = this.state.products;
      products.push({
        name: current.name.trim(),
        price: Number(current.price) >= 0 ? Number(current.price) : 0
      });

      this.props.getProducts(products);

      this.setState({
        current: {
          name: '',
          price: '',
          errors: {
            nameError: '',
            priceError: ''
          }
        },
        products: products
      });
    }
  }

  render() {
    return (
      <div className="add-product clearfix">
          <h1 className="add-product__header">Добавить продукт</h1>
          <div className="add-product__left clearfix">
            <div className="add-product__group">
              <TextField name="name" floatingLabelText="Продукт" onChange={this.handleFieldChange} fullWidth={true} errorText={this.state.current.errors.nameError} value={this.state.current.name}/>
            </div>
            <div className="add-product__group">
              <TextField name="price" floatingLabelText="Цена" onChange={this.handleFieldChange} type="number" fullWidth={true} errorText={this.state.current.errors.priceError} value={this.state.current.price}/>
            </div>
          </div>
          <div className="add-product__right">
            <RaisedButton className="add-product__btn" label="Добавить" onClick={this.handleClick} primary={true}/>
          </div>
      </div>
    )
  }
}

class Basket extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="table-container">
        <Table fixedHeader={false} fixedFooter={false} selectable={false}>
          <TableHeader displaySelectAll={false} adjustForCheckbox={false} enableSelectAll={false}>
            <TableRow>
              <TableHeaderColumn>Продукт</TableHeaderColumn>
              <TableHeaderColumn>Цена</TableHeaderColumn>
              <TableHeaderColumn>Цена со скидкой</TableHeaderColumn>
            </TableRow>
          </TableHeader>
          <TableBody displayRowCheckbox={false}>
            {this.props.products.map( (row, index) => (
              <TableRow key={index}>
                <TableRowColumn>{row.name}</TableRowColumn>
                <TableRowColumn>{row.price}</TableRowColumn>
                <TableRowColumn></TableRowColumn>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    )
  }
}

export default App;



